<template>
  <div class="me">
		<h1></h1>
		<div class="play">
		  <!-- Swiper -->
		  <div class="swiper-container">
		    <div class="swiper-wrapper">
		      <div class="swiper-slide">
		      	<img src="../../static/img/p1.jpg" alt="">
		      </div>
		      <div class="swiper-slide">
		      	<img src="../../static/img/p2.jpg" alt="">
		      </div>
		      <div class="swiper-slide">
		      	<img src="../../static/img/p3.png" alt="">
		      </div>
		      <div class="swiper-slide">
		      	<img src="../../static/img/p4.jpg" alt="">
		      </div>
		    </div>
		    <!-- Add Pagination -->
		    <div class="swiper-pagination"></div>
		  </div>
		</div>
  
  		<div class="info">
			<ul>
				<li>
					<span class="iconfont icon-collection_fill"></span>
					<br />
					<a href="#">我的收藏</a>
				</li>
				<li>
					<span class="iconfont icon-comments" style="color: #62a5e5;"></span>
					<br />
					<a href="#">我的评论</a>
				</li>
				<li>
					<span class="iconfont icon-praise_fill" style="color: #df8963;"></span>
					<br />
					<a href="#">我的点赞</a>
				</li>
				<li>
					<span class="iconfont icon-time-circle" style="color: #4faa93;"></span>
					<br />
					<a href="#">浏览历史</a>
				</li>
			</ul>
		</div>


		<div class="center">
			<div class="item">
				<a href="#">我的关注</a>
				<span> > </span>
			</div>
			<div class="item">
				<a href="#">消息通知</a>
				<span> > </span>
			</div>
			<div class="item">
				<a href="#">阅读公益</a>
				<span> > </span>
				<span style="font-weight: 500;">今日阅读一分钟</span>
			</div>
		</div>
	<div class="center">
			<div class="item">
				<a href="#">用户反馈</a>
				<span> > </span>
			</div>
			<div class="item">
				<a href="#">消息通知</a>
				<span> > </span>
			</div>
			<div class="item">
				<a href="#">阅读公益</a>
				<span> > </span>
				<span style="font-weight: 500;">今日阅读一分钟</span>
			</div>
		</div>

  </div>
</template>

<script>
// 引入swiper 组件
import Swiper from 'swiper';
import "swiper/dist/css/swiper.min.css";
export default {
  name: 'Me',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  // 钩子函数
  mounted: function () {
  	// 实例化 swiper
    var swiper = new Swiper('.swiper-container', {
      pagination: {
        el: '.swiper-pagination',
      },
    });
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	
  // 引入 公共样式
  @import '../assets/sass/base.scss';
	
	.me{
		h1{
			text-align: center;
			line-height: rem(150px);
		}
		.play{
			margin-top: rem(20px);
			.swiper-container{
				img{
					width: 100%;
				}
			}
		}
	}

//info
.info{
    height: rem(120px);
     border-bottom: rem(8px) solid #f6f6f6;
     ul {
         display: flex;
         overflow: hidden;
         li{
             width: 25%;
             text-align: center;
             padding-top: rem(20px);
             line-height: rem(40px);
             padding-bottom: rem(20px);
             span{
                 font-size: rem(40px);
                 color: #eeb169;
                 
             }
             a{
                 font-size: rem(30px);
                 color: #343434;
             }
         }
     }
}

//center
.center {
    height: rem(300px);
    border-bottom: rem(8px) solid #f6f6f6;
    .item{
        float: right;
        width: 94%;
        border-bottom: rem(2px) solid #f6f6f6;
        height: rem(100px);
        line-height: rem(100px);
        a{
            font-size: rem(30px);
            text-decoration: none;
            color: #343434;
        }
        span{
            float: right;
            margin-right: rem(24px);
            color: #a4a4a4;
            font-weight: 600;
        }
    }
    .item:nth-of-type(3){
        border: 0;
    }
}



</style>




