<template>
  <div class="show">
  	<header>		
		<div class="title">
			<div class="tab">
		      <div class="mo" v-if="top">置顶</div>
		      <div class="mo" v-else-if="good">精华</div>
		</div>
			{{title}}
		</div>
		<div class="bottom">
			<div class="time">发布于{{create_at}}</div>
			<div class="author">作者：{{author.loginname}}</div>
			<div class="liulan">{{visit_count}}次浏览</div>
			<div class="tabs">来自{{tabs[tab]}}</div>
		</div>
	</header>
	<div class="main">
		<div class="content" v-html="content"></div>
		<div class="count">
	  		<div class="coz">
				<div class="count_f">{{item && item.reply_count}}  回复</div>
	  		</div>
	  	</div>
	  	<div class="reply">
	  		<ul>
	  			<li v-for="item in (item && item.replies)">
	  				<div class="top">
	  					<img class="top_f" v-lazy="item.author.avatar_url" src="" alt="" />
	  					<p class="top_s">{{item && item.author.loginname}}</p>
	  					<p class="top_t" v-html="item && item.content"></p>
	  				</div>
	  			</li>
	  		</ul>
	  	</div>
	</div>
	
  </div>
</template>`

<script>
// 引入 取数据的模块
import {getTopicData} from '@/getdata/getTopic.js';
export default {
  name: 'Show',
  data () {
    return {
      top:'',
      good:'',
      time:'',
      title:'',
      author:'',
      visit_count:'',
      tab:'',
      create_at:'',
      content:'',
      	//主题列表
		  tabs:{
		 	ask:"问答",
			job:"招聘",
			share:"分享"
		  },
		  item:[]
    }
  },
//方法
	mounted:function(){
		var id = this.$route.params.id;
		getTopicData(id).then((response)=>{
			this.item = response.data.data;
    	this.title = response.data.data.title;
    	this.top = response.data.data.top;
    	this.good = response.data.data.good;
    	this.author = response.data.data.author;
    	this.visit_count =  response.data.data.visit_count;
    	this.tab = response.data.data.tab;
    	this.content = response.data.data.content;
    	this.create_at =response.data.data.create_at.substring(0,10);
		})
		
		
		
	}
	
  
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	// 引入 公共样式
  @import '../../assets/sass/base.scss';
  .show{
  	margin-top: rem(120px);
  	margin-bottom: rem(140px);
  	header{
  		overflow:hidden;
  		padding-bottom: rem(30px);
    	border-bottom: rem(1px) solid #ccc;  	
  	.tab{
        margin: rem(10px) rem(20px) 0 0;
        float:left;
        overflow: hidden;
        div{
            border-radius: rem(5px);
            background: #999;
            color: #fff;
            font-size: rem(22px);
            width: rem(65px);
            height: rem(40px);
            border-radius: rem(10px);
            text-align: center;
            line-height: rem(40px);
        }
        .mo{
            background: #80bd01;
            color:#fff;                 
    	}
    }
    .title{
    	float: left;
    	width: 96%;
    	overflow: hidden;
    	font-size: rem(36px);
    	padding: rem(8px) rem(20px);
    	line-height: rem(66px);
    	margin-right: rem(10px);
    	font-weight: bold;
    }
    .bottom{
    	/*height: rem(130px);*/
    	margin-left: rem(30px);
    	color: #333;

    	.time{
    		float: left;   		
    		margin-right: rem(20px);
    	}
    	.author{
    		float: left;
    		margin-right: rem(20px);
    	}
    	.liulan{
    		float: left;
    		margin-right: rem(20px);
    	}
    	.tabs{
    		float: left;
    	}
     }
   } 
  .main{
  		padding: rem(20px) rem(50px);
   		/*overflow: hidden;*/
	   	.content{
	   		margin-bottom: rem(100px);
	   		width: 100%; 
	   		word-break: break-all; 
	   		/deep/ .markdown-text{
	   				width: 100%;
			   		img{
			   			width: 100%;
			   		}	   			
	   		}	
	   }
	   .count{
  		width:100%;
  		padding-top:rem(30px);
  		padding-bottom:rem(30px);
  		background-color:#efefef;
  		margin:0 auto;
  		.coz{
  			margin-left:rem(15px);
  			font-size:rem(18px);
  		}
  	}
  	.reply ul{
  		width:100%;
  		background-color:#f6f6f6;
  		margin:0 auto;
  		overflow:hidden;
  		.top{
  			margin-top:rem(23px);
  			margin-left:rem(23px);
  			padding-bottom:rem(70px);
	  		border-bottom:rem(1px) solid lightgray;
	  		img{
	  			width:rem(90px);
	  		}
	  		.top_s{
	  			margin-top:rem(-80px);
	  			margin-left:rem(100px);
	  			font-size:rem(10px);
	  		}
	  		.top_t{
	  			margin-top:rem(55px);
	  			font-size:rem(16px);
	  		}
	  	}
  	}
	     
   }  
  }
</style>



