<template>
  <div class="index">
		<nav class="top">
			<ul>
				<router-link to='/index/' tag='li' class='mo'>
					<span class="iconfont icon-zonghe" style="color: #ff9532;"></span>
					<p>全部</p>
				</router-link>
				<router-link to='/index/good' tag='li'>
					<span class="iconfont icon-jinghua" style="color:#53b1f6;"></span>
					<p>精华</p>
				</router-link>
				<router-link to='/index/share' tag='li'>
					<span class="iconfont icon-fenxiang" style="color:#5dcd3e;"></span>
					<p>分享</p>
				</router-link>
				<router-link to='/index/ask' tag='li'>
					<span class="iconfont icon-57" style="color:#fd8454;"></span>
					<p>问答</p>
				</router-link>
				<router-link to='/index/job' tag='li'>
					<span class="iconfont icon-work" style="color:#fec533;"></span>
					<p>招聘</p>
				</router-link>
			</ul>
		</nav>
		 <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入 公共样式
  @import '../assets/sass/base.scss';
  // 顶部导航
  nav.top{
    position: fixed;
    top:0;
    left:0;
    width:100%;
    height: rem(100px);
    background: #f6f6f6;
    ul{
      display: flex;
      align-items: center;
      justify-content: space-around;
      height: rem(100px);
      li{
        font-size: rem(40px);
        font-family: '楷体';
        padding: rem(4px) rem(18px);
        border-radius: rem(20px);
        font-weight: bold;
        color:#80bd01;
        .iconfont{
        	width: rem(40px);
        	height: rem(40px);
        	display:block;
        	margin:0 auto;
        	margin-bottom: rem(4px);
        	font-size:rem(40px);
        	border-radius:50%;
        }
        &.router-link-exact-active{
          color:#fff;
          background: #80bd01;         
        }
      }
    }
  }

</style>



