<template>
  <div id="app">
    <router-view/>
    
    <nav class="footer">
    	<ul>
    		<router-link to='/index' tag='li' :class="resPath=='index'?'mo':''">
    			<span class="iconfont icon-shouye"></span>
    			<p>首页</p>
    		</router-link>
    		<router-link to='/gzh' tag='li' :class="resPath=='gzh'?'mo':''">
    			<span class="iconfont icon-gongzhonghao"></span>
    			<p>公众号</p>
    		</router-link>
    		<router-link to='/me' tag='li' :class="resPath=='me'?'mo':''">
    			<span class="iconfont icon-geren"></span>
    			<p>我的</p>
    		</router-link>
    	</ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
	//计算属性
	computed:{
		resPath:function(){
			var arr = this.$route.path.split('/');
			return arr[1];
		}
	}
  
}
</script>

<style lang="scss">
@import './assets/sass/base.scss';

/*底部导航*/
nav.footer{
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	height: rem(120px);
	background: #54d075;
	ul{
		display:flex;
		li{
			width: 33.33%;
			height: rem(120px);
			color: #fff;
			font-weight: bold;
			span{
				display:block;
				font-size: rem(70px);
				text-align: center;
			}
			p{
				font-size: rem(30px);
				line-height: rem(30px);
				text-align: center;
			}
			&.mo{
				color: #35c3a8;
				background: cornsilk;
			}
		}
	}
}

</style>



