<template>
  <div class="good">
	<!-- 主题列表 -->
    <topic-list :items="items"></topic-list>
    <infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>
	
// 引入 取数据的模块
import {getTopics} from '@/getdata/getTopic.js';
// 引入 主题列表组件
import topicList from '@/components/topics/List';
// 引入 分页滑动组件
import InfiniteLoading from 'vue-infinite-loading';
export default {
  name: 'Good',
  data () {
    return {
	//主题列表
     items:[],
     page:1,
     isShow:true
    }
  },
	//钩子函数
	mounted:function(){
		getTopics({tab:'good',limit:20,page:this.page}).then((response)=>{
			this.items = response.data.data;
		})
	},
	methods: {
    infiniteHandler($state) {
      setTimeout(() => {
        // 查询 全部主题数据
        getTopics({tab:'good',limit:25,page:++this.page}).then((response)=>{
          console.log(response.data.data);
          // 把查询到的数据放到 items里
          this.items = this.items.concat(response.data.data);
//        判断下一页还有没有数据
		  if(response.data.data.length == 0){
		  	this.isShow = false;
		  	return;
		  }
//		  再次调用请求的函数
          $state.loaded();
        });
      }, 500);
    },
  },
//	加载组件
	components:{
		topicList,InfiniteLoading
	}

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	@import '../../assets/sass/base.scss';
	@import '../../assets/sass/itemlist.scss';
	.good{
		margin-top: rem(100px);
		margin-bottom: rem(130px);
	}
</style>



